import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, ModalController, ToastController } from 'ionic-angular';

import { SoapService } from '../soap.service';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass } from '../../config';

/**
 * Generated class for the UangMukaListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-uang-muka-list',
  providers: [SoapService],
  templateUrl: 'uang-muka-list.html',
})
export class UangMukaListPage {

  userdataTPK: any;
  isLoading: Boolean = true;
  uangMukaList: Array<any> = [];
  fakeUsers: Array<any> = new Array(5);  
  batasAtas = 1;
  batasBawah = 10;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UangMukaListPage');
    this.storage.get('userdataTPK').then(val => {
      this.userdataTPK = val;    
      this.getUangMukaList('first', '');
    });
  }

  getUangMukaList(type, functionName) {    
    if (type == 'first' && functionName == '') {
      this.isLoading = true;  
    }        
    this.soapService
      .post(api_base_url, 'eoffice_uang_muka_list', {
        fStream: JSON.stringify(
          {
            usernameEDI : api_user, 
            passwordEDI : api_pass,
            id_user:this.userdataTPK['data']['IDUSER'],           
            no_surat: '',
            perihal: '',
            no_jkk: '',
            no_pertanggungjawaban: '',
            tgl_pengajuan : '',
            atas: this.batasAtas,
            bawah: this.batasBawah
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result)); 
        console.log(responData)       ;
        if (responData['rcmsg'] == "SUCCESS") {
          if (type == 'refresh' && functionName != '') {
            this.uangMukaList = [];
          }
          if (responData['data'].length > 0 ) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.uangMukaList.push(responData['data'][i]);
            }
          }          
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data cuti, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }        
        
      })
      .catch(error => {        
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan data cuti, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      
        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }

        this.isLoading = false;
      });
  }

  doInfinite(infiniteScroll) {
    if (this.uangMukaList.length >= 10) {
      this.batasAtas = this.batasBawah + 1;
      this.batasBawah = this.batasBawah + 10;
      this.getUangMukaList('infinite', infiniteScroll);
    } else {
      infiniteScroll.complete();
    }    
  }

  doRefresh(refresher) {
    this.batasAtas = 1;
    this.batasBawah = 10;
    this.getUangMukaList('refresh', refresher);    
  }

  searchUangMuka() {
    const modal = this.modalCtrl.create('SearchUangMukaPage');
    modal.present();
  }

}



